import React, { useState } from "react";
import { Button, Table, Modal, Form } from "react-bootstrap";
import styles from "../styles/shipping.module.css"; // Updated custom styles

const ShippingPage = () => {
  const [showModal, setShowModal] = useState(false);
  const [shipmentData, setShipmentData] = useState({});
  const [shipments, setShipments] = useState([]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setShipmentData((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const handleCloseModal = () => {
    setShowModal(false);
    setShipmentData({});
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setShipments((prevShipments) => [
      ...prevShipments,
      { ...shipmentData, id: prevShipments.length + 1 },
    ]);
    handleCloseModal();
  };

  return (
    <div className="container mt-5">
      <h1 className={`${styles.pageTitle} text-center mb-4`}>Shipping Management</h1>

      <div className="d-flex justify-content-end mb-3">
        <Button variant="primary" onClick={() => setShowModal(true)}>
          Create Shipment
        </Button>
      </div>

      <Table striped bordered hover>
        <thead>
          <tr>
            <th>#</th>
            <th>Shipment ID</th>
            <th>Recipient Name</th>
            <th>Address</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {shipments.map((shipment, index) => (
            <tr key={shipment.id}>
              <td>{index + 1}</td>
              <td>{shipment.id}</td>
              <td>{shipment.recipientName}</td>
              <td>{shipment.address}</td>
              <td>{shipment.status}</td>
              <td>
                <Button variant="info" size="sm" className="me-2" onClick={() => setShowModal(true)}>
                  Edit
                </Button>
                <Button
                  variant="danger"
                  size="sm"
                  onClick={() =>
                    setShipments(shipments.filter((s) => s.id !== shipment.id))
                  }
                >
                  Delete
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>

      <Modal show={showModal} onHide={handleCloseModal} className={styles.customModal} centered>
        <Modal.Header closeButton>
          <Modal.Title>{shipmentData.id ? "Edit Shipment" : "Create Shipment"}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={handleSubmit}>
            <Form.Group controlId="recipientName" className="mb-3">
              <Form.Label>Recipient Name</Form.Label>
              <Form.Control
                type="text"
                name="recipientName"
                value={shipmentData.recipientName || ""}
                onChange={handleChange}
                placeholder="Enter recipient name"
              />
            </Form.Group>

            <Form.Group controlId="address" className="mb-3">
              <Form.Label>Shipping Address</Form.Label>
              <Form.Control
                type="text"
                name="address"
                value={shipmentData.address || ""}
                onChange={handleChange}
                placeholder="Enter shipping address"
              />
            </Form.Group>

            <Form.Group controlId="status" className="mb-3">
              <Form.Label>Status</Form.Label>
              <Form.Select
                name="status"
                value={shipmentData.status || ""}
                onChange={handleChange}
              >
                <option value="">Select Status</option>
                <option value="Processing">Processing</option>
                <option value="Shipped">Shipped</option>
                <option value="Delivered">Delivered</option>
                <option value="Canceled">Canceled</option>
              </Form.Select>
            </Form.Group>

            <Button variant="primary" type="submit" className={styles.modalButtonPrimary}>
              {shipmentData.id ? "Update Shipment" : "Save Shipment"}
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
    </div>
  );
};

export default ShippingPage;
